<template>
  <tr class="text-uppercase">
    <th class="criterion">Criterion</th>
    <th class="aim">Aim</th>
    <th
      v-for="alternativeId in alternativesIds"
      :key="`alternative-${alternativeId}`"
      :colspan="subOptionsIds.length"
      class="alternative"
    >
      <v-icon v-if="state.data?.alternatives?.[alternativeId]?.icon">
        {{ state.data.alternatives[alternativeId].icon }}
      </v-icon>
      {{ getAlternatives[alternativeId]?.name || 'Unnamed' }}
    </th>
  </tr>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import {
  state,
  alternativesIds,
  subOptionsIds,
  getAlternatives
} from "@/store";

export default defineComponent({
  name: "TableListHead",
  setup() {
    return {
      state,
      alternativesIds: computed(() => alternativesIds()),
      subOptionsIds: computed(() => subOptionsIds()),
      getAlternatives: computed(() => getAlternatives())
    };
  }
});
</script>

<style scoped>

/* Header sticky */
.custom-table thead th {
  position: sticky;
  top: 0;
  background: white;
  z-index: 2;
  border-bottom: 2px solid #ccc;
  padding: 8px;
  text-align: left;
}
</style>


